/**
 * ä¾åé¨åå¤é¨å±åä½¿ç¨çåæ®è¿½è¸ªæå¡ç±»ï¼å¯è¾å¥åæ°èªå¨æ¥è¯¢å¹¶æ¾ç¤ºåæ®è¿½è¸ªæ¨¡ææ¡ï¼ä¹å¯ä»¥ä»è·ååæ®è¿½è¸ªæ¥è¯¢ç»æ
 */
@Injectable()
export class AifDocumentsTrackService {
    private serverIP: string = "";
    private baseUrl = '/api/runtime/aif/v1.0/associationcontroller/';

    public constructor(private http: HttpService, private modalService: BsModalService, private injector: Injector, private cfr: ComponentFactoryResolver, config: AifDocumentsTrackManagerModuleConfig) {
        this.serverIP = config == null || config.serverIP == null ? "" : config.serverIP;
    }

    /**
     * æ ¹æ®åæ°è¿½è¸ªå¹¶æ¾ç¤ºè¿½è¸ªæ¨¡ææ¡ï¼æ³¨æï¼è°ç¨è¯¥æ¹æ³æ¶åºåæ¶å¼ç¨TrackTypeæä¸¾ç±»
     * @param documentId åæ®ID
     * @param beType åæ®ç±»åç¼å·
     * @param trackType è¿½è¸ªç±»åï¼ä»å¯ç¨âUpSearchâï¼ä¸æ¥ï¼æâDownSearchâï¼ä¸æ¥)
     */
    public showResultDialog(documentId: string, beType: string, trackType: TrackType): BsModalRef {
        const cmpF = this.cfr.resolveComponentFactory(AifDocumentsTrackManagerComponent);
        const cmpR = cmpF.create(this.injector);

        if (trackType != TrackType.DownSearch && trackType != TrackType.UpSearch) {
            trackType = TrackType.DownSearch;
        }

        cmpR.instance.searchFromOut(documentId, beType, trackType);

        return this.modalService.show(cmpR, {
            title: 'è¿½è¸ª',
            width: 1100,
            height: 600,
            buttons: null,
            showButtons: false
        });
    }

    /**
     * æ ¹æ®åæ°è¿½è¸ªå¹¶å°æ¥è¯¢ç»æè¿åï¼æ³¨æï¼è°ç¨è¯¥æ¹æ³æ¶åºåæ¶å¼ç¨TrackTypeæä¸¾ç±»
     * @param documentId åæ®ID
     * @param beType åæ®ç±»åç¼å·
     * @param trackType è¿½è¸ªç±»åï¼ä»å¯ç¨âUpSearchâï¼ä¸æ¥ï¼æâDownSearchâï¼ä¸æ¥),
     */
    public getTrackedDocumentData(documentId: string, beType: string, trackType: TrackType): Observable<any> {
        if (trackType == TrackType.DownSearch) {
            return this.getTargetData(beType, documentId);
        } else {
            return this.getSourceData(beType, documentId);
        }
    }

    private getTargetData(srcBeType: string, srcDataId: string): Observable<any> {
        const url = this.serverIP + this.baseUrl + `GetTargetData/${srcBeType}&${srcDataId}`;
        return this.http.get(url);
    }

    private getSourceData(tarBeType: string, tarDataId: string): Observable<any> {
        const url = this.serverIP + this.baseUrl + `GetSourceData/${tarBeType}&${tarDataId}`;
        return this.http.get(url);
    }
}