import { Component, OnInit, Input, Output, OnChanges, SimpleChanges } from '@angular/core';
import { TrackType } from '../config/track-type.map';
import { AifDocumentsTrackService } from '../service/aif-documents-track.service';
import { TrackTypeTextMap } from '../config/track-type-text.map';
import { OrderInfo } from '../../entity/order-info';

/**
 * åæ®è¿½è¸ªä¸»ç»ä»¶
 */
@Component({
  selector: 'app-aif-documents-track-manager',
  templateUrl: './aif-documents-track-manager.component.html',
  styleUrls: ['./aif-documents-track-manager.component.css'],
})
export class AifDocumentsTrackManagerComponent implements OnInit, OnChanges {

  /**å­ç»ä»¶æ¾ç¤ºæ§å¶ */
  isHidden: boolean = true;
  isShowable: boolean = false;
  isResultEmpty: boolean = null;

  @Input()
  documentId: string = null;
  @Input()
  beTypeCode: string = null;
  @Input()
  selectedTrackType: TrackType = null;
  @Output()
  trackTypes = [];

  /**ä¼ éç»å­ç»ä»¶çæéæ©çè¿½è¸ªæ¹å¼ */
  selectedTrackTypeToChildren: string = "";

  /**ååºç»æè½¬åä¸ºç»ä»¶åä¼ è¾æ ¼å¼ */
  beTypeCodes: any[] = [];
  documents: { [beTypeCode: string]: any[] } = {};
  beTypeNames: { [beTypeCode: string]: string } = {};

  constructor(private trackService: AifDocumentsTrackService) {
    //åå§åè¿½è¸ªæ¹å¼ä¸ææ¡
    for (let item in TrackType) {
      this.trackTypes.push({ value: item, text: TrackTypeTextMap[item] });
    }
    //é»è®¤éæ©âä¸æ¥âæ¹å¼
    this.selectedTrackType = this.trackTypes[1].value;
    this.selectedTrackTypeToChildren = this.selectedTrackType;
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  search(): void {
    this.selectedTrackTypeToChildren = this.selectedTrackType;

    this.trackService.getTrackedDocumentData(this.documentId, this.beTypeCode, this.selectedTrackType).subscribe((data: OrderInfo[]) => {
      let tempDocuments: { [beTypeCode: string]: any[] } = {};
      let tempBeTypeNames: { [beTypeCode: string]: string } = {};
      let tempBeTypeCodes = [];

      if (this.isNullOrUndefined(data) == false && data.length > 0) {
        this.isResultEmpty = false;
        this.isShowable = true;
        this.isHidden = false;
        for (let item of data) {
          //æ¥è¯¢ç»æä¸ä¸ºç©ºæ¶
          if (this.isNullOrUndefined(tempDocuments[item.BeTypeCode]) == false) {
            tempDocuments[item.BeTypeCode].push(item);
          } else {
            tempBeTypeCodes.push(item.BeTypeCode);
            tempBeTypeNames[item.BeTypeCode] = item.BeTypeName;
            tempDocuments[item.BeTypeCode] = [item];
          }
        }
      } else {
        //æ¥è¯¢ç»æä¸ºç©ºæ¶
        this.isResultEmpty = true;
        this.isShowable = false;
        this.isHidden = true;
      }
      this.documents = tempDocuments;
      this.beTypeCodes = tempBeTypeCodes;
      this.beTypeNames = tempBeTypeNames;
    });
  }

  /**
   * ä¸ºå¤é¨æä¾çæ¹ä¾¿éå½è°ç¨çæ¥è¯¢æ¥å£
   * @param documentId 
   * @param beType 
   * @param trackType 
   */
  public searchFromOut(documentId: string, beType: string, trackType: TrackType): void {
    this.documentId = documentId;
    this.beTypeCode = beType;
    this.selectedTrackType = trackType;
    this.search();
  }

  /**
   * ä»æ¥è¯¢ç»æä¸­éæ©åæ®ç»§ç»­è¿½è¸ªæ¶ï¼ç¶ç»ä»¶æ¥æ¶å°å­ç»ä»¶ä¼ æ¥ç
   * å¯¹è±¡ï¼å¹¶èªå¨è°ç¨æ¥è¯¢æ¹æ³
   * @param $event 
   */
  trackGoingOn($event: any): void {
    this.documentId = $event.documentId;
    this.beTypeCode = $event.beTypeCode;
    this.selectedTrackType = $event.trackType;
    this.search();
  }

  private isNullOrUndefined(arg: any): boolean {
    return arg == null;
  }
}
