import { Component, OnInit } from "@angular/core";
import { AifPullSelectCreationRuleInternalService } from "../service/aif-pull-select-creation-rule.internal.service";
import { Subject } from "rxjs";
import { BsModalRef } from '@farris/ui-modal';
import { AifCreationRuleEntity } from '@gsp-aif/common-api';
import { CreationRuleResultEntityEx } from '../../entity/creation-rule-entity-ex';
import { GetCreationRuleArgs } from '../../entity/get-creation-rule-args';

@Component({
    selector: 'app-aif-pull-select-creation-rule',
    templateUrl: './aif-pull-select-creation-rule.component.html',
    styleUrls: ['./aif-pull-select-creation-rule.component.css']
})
export class AifPullSelectCreationRuleComponent implements OnInit {
    voTypeCodes: string[] = [];

    voTypeObjs: { [voType: string]: { "VoType": string, "VoName": string } } = {};

    getCreationRuleResultEntities: { [voType: string]: CreationRuleResultEntityEx[] } = {};

    selectedVoTypeCode: string = null;

    selectedGetCreationRuleResultEntity: CreationRuleResultEntityEx = null;

    bsModalRef: BsModalRef = null;

    subject: Subject<AifCreationRuleEntity> = new Subject();

    constructor(private crService: AifPullSelectCreationRuleInternalService) {

    }

    public getCreationRule(args: GetCreationRuleArgs) {
        this.crService.getCreationRule(args).subscribe(
            (data: CreationRuleResultEntityEx[]) => {
                for (let item of data) {
                    if (!this.voTypeCodes.includes(item.SourceVoType)) {
                        this.voTypeCodes.push(item.SourceVoType);
                        this.getCreationRuleResultEntities[item.SourceVoType] = [];
                        this.voTypeObjs[item.SourceVoType] = {
                            "VoType": item.SourceVoType,
                            "VoName": item.SourceVoName
                        };
                    }
                    this.getCreationRuleResultEntities[item.SourceVoType].push(item);

                }
                if (data.length > 0) {
                    this.selectedVoTypeCode = this.voTypeCodes[0];
                    this.selectedGetCreationRuleResultEntity = data[0];
                }
            }
        );
    }

    ngOnInit(): void {

    }

    changeVoTypeRadio(voTypeCode: string) {
        this.selectedVoTypeCode = voTypeCode;
        this.selectedGetCreationRuleResultEntity = !this.isNullOrUndefined(this.getCreationRuleResultEntities[this.selectedVoTypeCode]) && this.getCreationRuleResultEntities[this.selectedVoTypeCode].length > 0 ? this.getCreationRuleResultEntities[this.selectedVoTypeCode][0] : null;
    }

    clickOk() {
        this.subject.next(this.selectedGetCreationRuleResultEntity as AifCreationRuleEntity);
        this.subject.complete();
        this.bsModalRef.close();
    }

    clickClose() {
        this.bsModalRef.close();
    }

    private isNullOrUndefined(arg: any): boolean {
        return arg == null;
    }

}